/* Navbar */
.navbar{
	position: fixed;
	width: 100%;
	display:flex;
	justify-content: space-between;
	background-color: black;
	align-items: center;
	color: var(--color-white);
	padding: 16px;
	transition: all var(--animation-duration) ease-in-out;
	z-index: 1;
}
.navbar.navbar--dark{
	background-color: var(--color-pink);
	padding: 8px;
}
.navbar__menu{
	display: flex;
	font-weight: bold;
	

}
.navbar__logo{
	font-size: var(--font-medium);
	font-weight: var(--weight-semi-bold);
	margin-left: 10px;
}
.navbar__logo__name{
	margin-left: 10px;
}
.navbar__menu__item{
	padding:8px 12px;
	margin: 0 4px;
	color: var(--color-white);
	cursor: pointer;
	border-radius: var(--size-border-radius);
	border:1px solid transparent;
}

.navbar__menu__item:hover{
	background-color: white;
	border-color: var(--color-white);
	transition:  all var(--animation-duration) ease-in-out;
}

.navbar__toggle-btn{
	position: absolute;
	right: 32px;
	font-size: 24px;
	color: white;
	top: 24px;
	display: none;
}



/*  nav 움직일때 변하기 */
.navbar__white {
	position: fixed;
	width: 100%;
	display:flex;
	justify-content: space-between;
	background: transparent ;
	background-color: white;
	align-items: center;
	padding: 16px;
	transition: all var(--animation-duration) ease-in-out;
	z-index: 1;
}
.navbar__white a {
	color: black;
}