/* GLobal */
:root{
  /* Color */
--color-white: #ffffff;
--color-light-white: #eeeeee;
--color-dark-white: #bdbdbd;
--color-pink: #fe918d;
--color-dark-pink: #ff6863;
--color-dark-grey: #4d4d4d;
--color-grey: #616161;
--color-light-grey: #7c7979;
--color-blue: #73aace;
--color-yellow: #fff7d1;
--color-orange: #feb546;
--color-black: #000000;

/* Font size */
--font-large: 48px;
--font-medium: 28px;
--font-regular: 18px;
--font-small: 16px;
--font-micro: 14px;

/* Font weight */
--weight-bold: 700;
--weight-semi-bold: 600;
--weight-regular: 400;
/* Size */
--size-border-radius:4px;
/* Annimation */
--animation-duration:300ms;
}
/* Universal tags */
* {
 box-sizing: border-box;
}
body{
 margin:0;
font-family: 'Open Sans', sans-serif;
cursor:default;
}
a{
 text-decoration: none;
 color: var(--color-light-grey)
}
ul{
 list-style: none;
 padding-left: 0;
}
button{
 background-color: transparent;
 cursor: pointer;
 border: none;
 outline: none;
}
/* Typography */
h1{
 font-size: var(--font-large);
 font-weight: var(--weight-bold);
 color: var(--color-black);
 margin:16px 0;
}
h2{
 font-size: var(--font-medium);
 font-weight: var(--weight-semi-bold);
 color: var(--color-black);
 margin: 8px 0 ;
}
h3{
 font-size: var(--font-regular);
 font-weight: var(--weight-regular);
 color: var(--color-black);
 margin: 8px 0 ;
}
p{
 font-size: var(--font-regular);
 font-weight: var(--weight-regular);
 color: var(--color-black);
 margin: 8px 0 ;
}

/* common section */
.section {
	text-align: center;
	padding: 50px;
	
	margin:auto

}
.section__container{
	max-width: 1200px;
	margin: auto;
}