#skills{
	background-color: var(--color-yellow);
}
.skillset{
	display: flex;
	background-color: var(--color-light-grey)
	;
	color: var(--color-light-white);
	margin: 20px 0;
}
.skillset__title{
	color:var(--color-white);

}
.skillset__left{
	flex-basis:60%;
	background-color: var(--color-dark-grey);
	padding: 20px 40px;
}

.skillset__right{
	flex-basis:40%;
}
.tools{
	background-color: var(--color-grey);
}
.skill__value{
	
	height: 3px;
	background-color: var(--color-orange);
}
.skill {
	margin-bottom: 32px;
}
.skill__description{
	display: flex;
	justify-content: space-between;
}
.skill__bar{
	width: 100%;
	height:3px;
	background-color: var(--color-grey);
}
.tools,
.etc {
	padding: 20px;
}