/* list 전체 */
.list{
	width: 100%;
}

table {
	width: 97%;
	border: 1px solid #444444;
	border-collapse: collapse;
	margin-left: auto;
    margin-right: auto;
	
}
th{
	background-color: gainsboro;
}
th, td {
	border-bottom: 1px solid #444444;
	border-left: 1px solid #444444;
	padding: 3px;
}
td{
	text-align: center;
}
.th-1{
	width: 80px;
}
.th-2{
	width: 500px;
}
.th-5{
	width: 80px;
}
/* 리스트 버튼 */
.list__buttons{
	display: flex;
	justify-content: right;
	margin-right: 14px;
	margin-top: 5px;
}

.list__buttons>button{
	font-weight: bold;
	border: 1px solid black;
	margin-left: 8px;
}


/* 리스트 넘버  */

.list__number>ul{
	display: flex;
	justify-content: center;
	margin-left: auto;
	margin-right: auto;
	cursor: pointer;
	
}
.list__number>ul>li{
	margin-left: 9px;
}