/* write 전체화면 */
.edit{
	width: 100%;
	height:700px;
	background-color: gray;
}

/* edit 컨텐츠 */

.edit__content{
/* border: red 3px solid; */
width: 730px;
height: 700px;
margin-left: auto;
margin-right: auto;
padding-top: 100px;
/* padding-bottom: auto */
}




.edit__example{
	width: 748px;
	height: 40px;
	background-color: white;
	text-align: center;
	line-height: 40px;
	background-color: rgb(235, 235, 235);
	margin-left: 1px;

}



.edit__input__title{
	display: block;
	width:750px;
	height: 40px;
}
#edit__input__content{
	margin-top: 10px;
}

.edit__buttons{
	margin-left: 500px;
	margin-top: 5px;
}
.edit__buttons>button {
	display: inline;
	background-color: white;
	width:100px;
	height: 30px;
	color: black;
	border-radius:10%;
	cursor: pointer;
	margin-left: 5px;
	

}