.login{
	background-color: gray;
	display: flex;
  justify-content: center;
  align-items: center;
	
}
.content{
	margin: 10px 0px;
	width: 300px;
	height: 300px;
	background-color: white;
	padding: 40px 40px;
	border: 0.5rem solid ;
	
}
.content>div{
	margin-bottom: 15px;
}
.content>div>label{
	font-size: 17px;
	font-weight: bold;
}
.login__button{
	margin-left: 30px;
	margin-top: 20px;
	background-color: gray;
	width:100px;
	height: 30px;
	color: white;
	border-radius:10%;
	cursor: pointer;
	
}
